
import Scroll from '../scrollAll'
import XHeader from 'vux/src/components/x-header/index.vue'

import {mapMutations} from 'vuex'

export default {
name: "select",
components: {
Scroll,
XHeader
},
data() {
return {
page: 1,
count: 20,
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
list: [],
customerId:''
}
},
mounted() {
this.customerId = this.$route.query.id
this.GetPagedCustomerLinkManList()
},
methods: {
...mapMutations(['setChooseLinkMan']),
GetPagedCustomerLinkManList() {
axios.get(`/Customer/GetPagedCustomerLinkManList`, {
params: {
page: this.page,
count: this.count,
customerId:this.customerId
}
}).then(res => {
console.log(res.data)
if (res.data.State === 1) {
if (this.page === 1) {
this.list = res.data.Data.List
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.scroll.finishPullUp();
} else {
this.list.push(...res.data.Data.List)
}
this.$refs.scroll.forceUpdate();
this.$refs.scroll.refresh();
}
}).catch(e => {
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate();
this.$vux.toast.show({
text: '网络异常',
type: 'warn'
})
})
},
getData(index) {
if (index === 1) {
this.page = 1
} else {
this.page++
}
this.GetPagedCustomerLinkManList()
},
saveSelectLinkman(data) {
this.setChooseLinkMan(data)
this.$router.replace('/order')
}
}
}



